.container{
    width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.item{
    display: flex;
    align-items: center;
    margin-top: 5px;
    cursor: pointer;
    width: 495px;
    height: 140px;
    transition: all 0.2s;
    box-sizing: border-box;
    box-shadow:0 0 3px rgba(0,0,0,0.3); 
    
}
.item:hover{
    transform: scale(1.02);
}
.itemtext{
    margin-left: -25px;
    list-style: none;
}
.write{
    color: gray;
    font-size: 14px;
}
.itemimage{
    width: 230px;
    height: 140px;
    box-sizing: border-box;
}

@media all and (max-width:767px) { 
    .container{
        width: 500px;
    }
}

