.container{
    width: 1000px;
    margin: 0 auto;
    background-color: black;
    height: 50px;
    display: flex;
    align-items: center;
}
.text{
    margin-left: 10px;
    margin-right: 20px;
    display: inline-block;
    font-size: 20px;
    color: white;
}
.search{
    outline: none;
    height: 33px;
    width: 765px;
    box-sizing: border-box;
    font-size: 20px;
}
.button{
    border: none;
    height: 33px;
    width: 40px;
    box-sizing: border-box;
    outline: none;
    font-size: 20px;
    background-color: rgb(95, 95, 95);
    color: rgb(202, 202, 202);
    cursor: pointer;
}
.logo{
    font-size: 30px;
    color: red;
    margin-left: 20px;
}
@media all and (max-width:767px) { 
    .container{
        width: 500px;
    }
    .text{
        margin-left: 5px;
        margin-right: 10px;
        font-size: 15px;
    }
    .search{
        width: 300px;
        font-size: 15px;
    }
    .button{
        border: none;
        height: 33px;
       
    }
}
