.container{
    width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
}
.youtube{
    display: block;
    margin-top: 2px;
}
.item{
    display: flex;
    align-items: center;
    margin-top: 5px;
    cursor: pointer;
    width: 340px;
    transition: all 0.2s;
    box-sizing: border-box;
    box-shadow:0 0 3px rgba(0,0,0,0.3); 
    margin: 0 auto;
    
}
.item:hover{
    transform: scale(1.02);
}
.itemtext{
    margin-left: -25px;
    list-style: none;
    margin-right: 10px;
}
.write{
    color: gray;
    font-size: 14px;
}
.itemimage{
    width: 120px;
    height: 90px;
    box-sizing: border-box;
}

@media all and (max-width:767px) { 
    .container{
        width: 500px;
        flex-direction: column;
    }
    .youtube{
        width: 500px;
        height: 260px;
    }
    .item{
        margin-top: 5px;
        width: 495px;
    }

}

